import { NavItems } from './Nav'
import { AppContext } from './AppProvider'

const OverlayMenu = (props) => {
  return (
    <AppContext.Consumer>
      {(context) => (
        <ul className={`${props.className} ${context.state.showMenu ? 'show' : ''}`}>
          <NavItems />
        </ul>
      )}
    </AppContext.Consumer>
  )
}

export default OverlayMenu
